<template>
  <div class="question-container">
    <h1 v-html="vraag.vraag"></h1>
    <button v-for="(antwoord, index) in vraag.antwoorden" :key="index" @click="response(vraag, antwoord, index)" :disabled="inResponse" :class="[buttonStyling(antwoord, index), {longQuestionStyle: vraag.isLong}]">
      {{antwoord.text}}
    </button>
    <div class="question-response">
      <span class="question-response__result">{{ responseResult }}</span><br />
      <span v-html="userResponse"></span>
    </div>
    <button v-show="inResponse" @click="volgende()" id="volgende">
      Volgende
    </button>
  </div>
</template>

<script>
import EventBus from '../event-bus'

export default {
  name: 'Question',
  props: ['vraag'],
  data () {
      return {
        responseResult: '',
        userResponse: '',
        inResponse: false,
        antwoord: [],
        checked: undefined,
        longQuestion: false,
      }
  },
  methods: {
    response (vraag, antwoord, index) {
      this.antwoord = antwoord
      this.inResponse = true
      if (vraag.isLong) {
        this.longQuestion = true
      }
      if (antwoord.correct) {
        this.responseResult = 'GOED'
      } else {
        this.responseResult = 'HELAAS'
      }
      this.userResponse = antwoord.uitleg
      vraag.antwoorden[index].checked = true
    },
    volgende () {
      var result = false
      if (this.antwoord.correct) {
        result = true
      }
      this.$emit('someEvent', result)
      this.antwoord.checked = false
      this.longQuestion = false
    },
    reset () {
      this.userResponse = ''
      this.inResponse = false
      this.checked = undefined
      this.responseResult = ''
      this.vraag.antwoorden.checked = false
    },
    buttonStyling (antwoord, index) {
      if (this.inResponse && this.longQuestion) {
        return {
          correct: antwoord.checked,
          hidden: !antwoord.checked,
        }
      } else if (this.inResponse) {
        return {correct: antwoord.checked}
      } else {
        return {correct: antwoord.checked}
      }
    }
  },
  computed: {
  },
  mounted () {
    EventBus.$on('reset', this.reset)
  }
}
</script>

<style scoped>
  h1 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 5vh;
    line-height: 5.5vh;
    margin-bottom: 20px;
}
  button {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 3.5vh;
    color: white;
    margin: 10px;
    background: #b3b4b3;
    border-radius: 10px;
    padding: 10px 20px;
    border: none;
    max-width: 80vw;
  }

    button:disabled {
      opacity: 0.5;
    }

  #volgende {
    font-size: 3vh;
    padding: 5px 15px;
    background: #0095ad;
  }

    .correct {
      background: #1fabe2;
      opacity: 1 !important;
    }

    .hidden {
      display: none;
    }

    .longQuestionStyle {
      text-align: left;
      width: 50vw;
      font-size: 2.8vh;
      margin: 5px;
      line-height: 3vh;
    }

  .question-container {
    padding: 0px 50px;
    flex: 1;
  }

  .question-response {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 3vh;
    font-weight: 300;
    margin: 15px 0px;
  }

    .question-response__result {
      font-family: 'Yanone Kaffeesatz', sans-serif;
      font-size: 3vh;
      font-weight: 400;
      text-transform: uppercase;
    }
</style>



