<template>
  <div id="app">
    <header>
      <img src='./assets/logo.png' @click="reset" height="100%">
    </header>
    <quiz/>
    <footer>
      <div id="footer-tagline" v-show="tagLineEnabled"></div>
    </footer>
  </div>
</template>

<script>
import Quiz from './components/Quiz'
import EventBus from './event-bus'

export default {
  name: 'App',
  components: {
    Quiz
  },
  data () {
    return {
      tagLineEnabled: true
    }
  },
  methods: {
    reset () {
      if (!this.tagLineEnabled) {
        this.tagLineEnabled = true
      }
      EventBus.$emit('reset')
    },
    disableTagline () {
      if (this.tagLineEnabled) {
        this.tagLineEnabled = false
        return false
      } else {
        this.tagLineEnabled = true 
        return true
      }
    }
  },
  mounted () {
    EventBus.$on('disableTagline', this.disableTagline)
  }
}
</script>

<style>

@import url('https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400');

body {
  background-image: url("assets/background.jpg");
  background-size: cover;
  margin: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 25px;
  font-weight: 300;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  height: 15vh;
}

  header img {
    margin-left: 60px;
  }



footer {
  background-image: url("assets/footer.png");
  background-size:100%;
  background-position: bottom;
  background-repeat: no-repeat;
  height: 20vh;
}

  #footer-tagline {
    background-image: url("assets/footer_tagline.png");
    background-size:100%;
    background-position: bottom;
    background-repeat: no-repeat;
    height: 20vh;
  }

</style>



