<template>
  <div class="quiz">
    <div v-show="vraagIndex === undefined" class="intro-page">
      <h1>Ben jij een myopie management expert? <br>Doe de test.</h1>
      <button @click='start' id="start">Start</button>
    </div>
    <question v-show="index === vraagIndex" 
              v-for="(vraag, index) in vragen" 
              :vraag="vraag" 
              :key="index"
              v-on:someEvent="parentHandler">
    </question>
    <div v-show="showScorePage">
        <h1>Jouw score:</h1>
        <div class="result">
          <h2>{{ scoreCorrect }}</h2>
          <span>van de {{ totalQuestions }} vragen goed</span>
        </div>
        <div class="score-result">{{ scoreResult }}</div>
        <div class="cta">Een goede specialist kijkt verder. Wij weten hoe. Geef je op bij onze commitment wall om één van de eerste myopie management experts in Nederland te worden.</div>
        <!-- <ol>
          <li v-for="(wrong, index) in wrongAnswers" :key="index">
            {{ wrong }}
          </li>
        </ol> -->
        <button @click="reset" id="opnieuw">Start opnieuw</button>
    </div>
    <div class="locationPath">
      <ul v-show="showPath" >
        <li v-for="(vraag, index) in vragen" :key="index" class="dot" :class="{active : index === vraagIndex }"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import Question from './Question' 
import EventBus from '../event-bus'

export default {
  name: 'Quiz',
  components: {
    Question
  },
  data () {
    return {
      vragen:[
        {
        vraag: 'Wat is op dit moment de meest voorkomende oogafwijking in de wereld?',
        antwoorden:[
          {text:'Presbyopie', uitleg:'“myopie” is het juiste antwoord.', checked: false},
          {text:'Myopie', uitleg:'Myopie is de meest voorkomende oogafwijking.', correct: true, checked: false},
          {text:'Computer Vision Syndrome (CVS)', uitleg:'“myopie” is het juiste antwoord. ', checked: false}
        ],
        isLong: false},
        {
        vraag: 'Welk percentage jongeren is op dit moment bijziend in Europa?',
        antwoorden:[
          {text:'40%', uitleg:'Helaas ligt het percentage hoger: maar liefst 50% van de jongeren in Europa is bijziend. In Azië is dat zelfs 80-90%! Een verdubbeling ten opzichte van zo’n 15 jaar geleden.'},
          {text:'50%', uitleg:'Inmiddels is al zeker 50% van de jongeren in Europa bijziend. In Azië is dat zelfs 80-90%! Een verdubbeling ten opzichte van zo’n 15 jaar geleden.', correct: true},
          {text:'60%', uitleg:'Het percentage ligt gelukkig wat lager, maar die kant gaat het wel snel op. Maar liefst 50% van de jongeren in Europa is al bijziend. In Azië is dat zelfs 80-90%!Een verdubbeling ten opzichte van zo’n 15 jaar geleden.'}
        ],
        isLong: false},
        {
        vraag: 'De verwachting is dat in het jaar 2050 -(vul in)- miljard mensen myoop zijn:',
        antwoorden:[
          {text:'6,81 miljard', uitleg:'De voorspelling is dat een schrikbarend aantal van 4,76 miljard mensen in 2050 myoop is.'},
          {text:'5,28 miljard', uitleg:'De voorspelling is dat een schrikbarend aantal van 4,76 miljard mensen in 2050 myoop is.'},
          {text:'4,76 miljard', uitleg:'Goed! Een schrikbarend hoog aantal, wat we absoluut moeten proberen te voorkomen.', correct: true}
        ],
        isLong: false},
        {
        vraag: 'De meeste ouders weten wat ze kunnen doen tegen de verergering van bijziendheid bij hun kinderen.',
        antwoorden:[
          {text:'Klopt', uitleg:'Helaas is uit recent gedragsonderzoek naar bijziendheid en de risicoperceptie van ouders gebleken dat het merendeel van de ouders niet weet wat de kans op (ernstige) bijziendheid bij hun kind zou kunnen verlagen. Goede voorlichting is daarom essentieel. '},
          {text:'Klopt niet', uitleg:'Inderdaad. Uit recent gedragsonderzoek naar bijziendheid en de risicoperceptie van ouders bleek dat het merendeel van de respondenten niet weet wat de kans op (ernstige) bijziendheid bij hun kind zou kunnen verlagen. Goede voorlichting is daarom essentieel.', correct: true},
          {text:'Geen idee', uitleg:'Helaas is uit recent gedragsonderzoek naar bijziendheid en de risicoperceptie van ouders gebleken dat het merendeel van de ouders niet weet wat de kans op (ernstige) bijziendheid bij hun kind zou kunnen verlagen. Goede voorlichting is daarom essentieel. '}
        ],
        isLong: false},
        {
        vraag: 'Hoeveel meer kans maken myopen in de range van -5,00 D tot -7,00 D op netvliesloslating t.o.v. laag myopen?',
        antwoorden:[
          {text:'5,8 keer meer kans', uitleg:'Uit een onderzoek van Flitcroft (2012) blijkt dat deze groep myopen maar liefst 21,5 keer meer kans heeft op netvliesloslating, met mogelijk ernstige gevolgen voor de visus. Bij meer dan -7,00 D is dat maar liefst 44,2 keer. '},
          {text:'21,5 keer meer kans', uitleg:'Klopt! Uit een onderzoek van Flitcroft (2012) blijkt dat bij myopen in de range van -5,00 D tot -7,00 D de kans op netvliesloslating 21,5 keer groter is. Bij meer dan -7,00 D is dat maar liefst 44,2 keer. ', correct: true},
          {text:'30,1 keer meer kans', uitleg:'Uit een onderzoek van Flitcroft (2012) blijkt dat deze groep myopen maar liefst 21,5 keer meer kans heeft op netvliesloslating, met mogelijk ernstige gevolgen voor de visus. Bij meer dan -7,00 D is dat maar liefst 44,2 keer. '}
        ],
        isLong: false},
        {
        vraag: 'Hoeveel soorten myopie management opties zijn er?',
        antwoorden:[
          {text:'3', uitleg:'Hoewel er helaas (nog) geen geneesmiddel voor myopie is, zijn er wel 3 soorten opties om de toename van progressieve myopie af te remmen: Lifestyle advies, Atropine oogdruppels en Optische interventie. ', correct: true},
          {text:'4', uitleg:'Hoewel er helaas (nog) geen geneesmiddel voor myopie is, zijn er wel 3 soorten opties om de toename van progressieve myopie af te remmen: Lifestyle advies, Atropine oogdruppels en Optische interventie.'},
          {text:'5', uitleg:'Hoewel er helaas (nog) geen geneesmiddel voor myopie is, zijn er wel 3 soorten opties om de toename van progressieve myopie af te remmen: Lifestyle advies, Atropine oogdruppels en Optische interventie.'}
        ],
        isLong: false},
        {
        vraag: 'Welke optische interventies zijn het meest geschikt om progressieve myopie zo veel mogelijk af te remmen?',
        antwoorden:[
          {text:'Ortho-k & speciale zachte multifocale contactlenzen', uitleg:'Klopt! Deze interventies zijn inderdaad het meest geschikt. Een interventie is succesvol als de remming van progressieve myopie minstens 50% reductie of meer is. Multifocale brillenglazen halen dit niet (20-30%).', correct: true},
          {text:'Speciale zachte multifocale contactlenzen & multifocale brillenglazen', uitleg:'Soms wordt gesuggereerd dat multifocale brillenglazen een positief effect hebben op myopie remming. Echter, de gemeten reductie van 20-30% haalt niet het criterium van minstens 50%. Daarnaast wordt een bril door een kind vaak als beperkend ervaren.'},
          {text:'Ortho-k & multifocale brillenglazen', uitleg:'Soms wordt gesuggereerd dat multifocale brillenglazen een positief effect hebben op myopie remming. Echter, de gemeten reductie van 20-30% haalt niet het criterium van minstens 50%. Daarnaast wordt een bril door een kind vaak als beperkend ervaren.'}
        ],
        isLong: true},
        {
        vraag: 'Waarom is buitenspelen gezond voor het afremmen van progressieve myopie?',
        antwoorden:[
          {text:'Door de verhoogde doorbloeding krijgt het oog een betere bloedtoevoer wat een positief effect heeft op het netvlies.', uitleg:'Het juiste antwoord is vanwege de lichtintensiteit: binnenshuis is die intensiteit ongeveer 500 lux, buitenshuis is dit overdag 15.000-40.000 lux. Het positieve effect wordt toegeschreven aan de vitamine D en dopamine die een gunstig effect hebben op het netvlies.'},
          {text:'De verhoogde lichtintensiteit buitenshuis heeft een positief effect op het netvlies.', uitleg:'Je antwoord is goed. Buitenspelen is gezond i.v.m. de lichtintensiteit buiten. Binnenshuis is die intensiteit ongeveer 500 lux. Buitenshuis is dit overdag 15.000-40.000 lux. Het positieve effect wordt toegeschreven aan de vitamine D en dopamine, die een gunstig effect hebben op het netvlies.', correct: true},
          {text:'Buitenspelen is goed voor de weerstand.', uitleg:'Het juiste antwoord is vanwege de lichtintensiteit: binnenshuis is die intensiteit ongeveer 500 lux, buitenshuis is dit overdag 15.000-40.000 lux. Het positieve effect wordt toegeschreven aan de vitamine D en dopamine die een gunstig effect hebben op het netvlies.'}
        ],
        isLong: true},
        {
        vraag: 'Wat is de gemiddelde aslengte van het oog?',
        antwoorden:[
          {text:'23 millimeter', uitleg:'Juist! 23 millimeter is inderdaad de gemiddelde aslengte van het oog. Bij hoogbijzienden (-6,00 D) is dit 26 mm. De kans op slechtziendheid is erg groot vanaf 30 mm.', correct: true},
          {text:'24 millimeter', uitleg:'De gemiddelde aslengte van het oog is 23 millimeter. Bij hoogbijzienden (-6,00 D) is dit 26 mm. De kans op slechtziendheid is erg groot vanaf 30 mm.'},
          {text:'26 millimeter', uitleg:'De gemiddelde aslengte van het oog is 23 millimeter. Bij hoogbijzienden (-6,00 D) is dit 26 mm. De kans op slechtziendheid is erg groot vanaf 30 mm.'}
        ],
        isLong: false},
        {
        vraag: 'Hoeveel % minder kans loop je op ernstige netvliesveranderingen met slechts 1 diopter reductie? (bijvoorbeeld -3,00 D i.p.v. -4,00 D).',
        antwoorden:[
          {text:'12% minder kans', uitleg:'Het is zelfs nog veel meer. 1 diopter verschil betekent gemiddeld maar liefst 42% minder kans op ernstige netvliesveranderingen.'},
          {text:'25% minder kans', uitleg:'Het is zelfs nog veel meer. 1 diopter verschil betekent gemiddeld maar liefst 42% minder kans op ernstige netvliesveranderingen.'},
          {text:'42% minder kans', uitleg:'Inderdaad, 1 diopter verschil betekent gemiddeld maar liefst 42% minder kans op ernstige netvliesveranderingen.', correct: true}
        ],
        isLong: false},
        {
        vraag: 'Wanneer vindt bij kinderen de grootste toename van myopie plaats?',
        antwoorden:[
          {text:'Tussen de 6 en 17 jaar oud', uitleg:'Klopt! Uit onderzoek van o.a. Holden et al. (2016) blijkt dat bij kinderen tussen de 6 en 17 jaar oud de toename van myopie het grootst is.', correct: true},
          {text:'Tussen de 2 en 14 jaar oud', uitleg:'Uit onderzoek van o.a. Holden et al. (2016) blijkt dat bij kinderen tussen de 6 en 17 jaar oud de toename van myopie het grootst is.'},
          {text:'Tussen de 10 en 18 jaar oud', uitleg:'Uit onderzoek van o.a. Holden et al. (2016) blijkt dat bij kinderen tussen de 6 en 17 jaar oud de toename van myopie het grootst is.'}
        ],
        isLong: false},
        {
        vraag: 'Wie wordt gezien als de belangrijkste invloedfactor bij de keuze die ouders en verzorgers maken voor een zichthulpmiddel voor hun kind?',
        antwoorden:[
          {text:'De huisarts', uitleg:'Ondanks dat allerlei invloedfactoren meespelen, blijkt uit het onderzoek dat de oogspecialist door ouders en verzorgers wordt gezien als de belangrijkste beïnvloeder bij de keuze voor een zichthulpmiddel voor hun kind.'},
          {text:'Familie & vrienden', uitleg:'Ondanks dat allerlei invloedfactoren meespelen, blijkt uit het onderzoek dat de oogspecialist door ouders en verzorgers wordt gezien als de belangrijkste beïnvloeder bij de keuze voor een zichthulpmiddel voor hun kind.'},
          {text:'De oogspecialist', uitleg:'Inderdaad! De oogspecialist wordt gezien als de belangrijkste beïnvloeder bij deze keuze. Hoe beter geïnformeerd ouders en verzorgers zijn over bijziendheid, hoe eerder ze signalen en risico’s herkennen.', correct: true}
        ],
        isLong: false},
        {
        vraag: 'Moeder en zoon (10) komen voor een nieuwe bril. Zijn oude bril (-1,25 D) is kapotgegaan bij het voetballen. Wat doe je?',
        antwoorden:[
          {text:'Je loopt naar het rek met kindermonturen voor een nieuwe bril.', uitleg:'Een bril en (standaard) contactlenzen werken niet tegen progressieve bijziendheid. Door een risicoanalyse te doen kun je een behandeling op maat aanbieden met het oog op zijn toekomstige visus.'},
          {text:'Je test zijn risico op progressieve bijziendheid en kiest een passende strategie op basis van de uitslag.', uitleg:'Correct! Een bril en (standaard) contactlenzen werken niet bij progressieve bijziendheid en zijn ook nog eens beperkend. Op basis van zijn risicoprofiel kun je een behandeling op maat aanbieden met het oog op zijn toekomstige visus.', correct: true},
          {text:'Je oppert om over te stappen op (standaard) contactlenzen zodat hij vrijer kan sporten.', uitleg:'Een bril en (standaard) contactlenzen werken niet tegen progressieve bijziendheid. Door een risicoanalyse te doen kun je een behandeling op maat aanbieden met het oog op zijn toekomstige visus.'}
        ],
        isLong: true},
        {
        vraag: 'Een gezin komt de winkel binnen. Beide ouders met bril (sterk bijziend), dochtertje (5) zonder oogafwijking. Moeder zoekt een nieuw montuur. Hoe reageer je?',
        antwoorden:[
          {text:'Je helpt de moeder met het uitzoeken van een nieuwe bril.', uitleg:'Als één ouder bijziend is, verhoogt het risico op bijziendheid bij het kind met 31% en zelfs met 46% als beide ouders bijziend zijn. Voorlichten en monitoren is daarom belangrijk.', checked: false},
          {text:'Je vertelt dat er een leuke actie is met tweede bril halve prijs; wellicht interessant voor de vader.', uitleg:'Als één ouder bijziend is, verhoogt het risico op bijziendheid bij het kind met 31% en zelfs met 46% als beide ouders bijziend zijn. Voorlichten en monitoren is daarom belangrijk.', checked: false},
          {text:'Je helpt de moeder, maar geeft ook informatie over myopie management en stelt een monitoringsplan voor hun dochtertje voor.', uitleg:'Goed! Als één ouder bijziend is, verhoogt het risico op bijziendheid bij het kind met 31%. Als beide ouders bijziend zijn is dit zelfs 46%. Voorlichten en monitoren is daarom belangrijk.', correct: true, checked: false}
        ],
        isLong: true},
      ],
      scoreResults: {
        above: 'Gefeliciteerd! Een mooie score. Wil je op de hoogte blijven van de nieuwste ontwikkelingen op het gebied van myopie management? Vraag het ons; wij helpen je graag verder.',
        below: 'Er is veel informatie over myopie management beschikbaar. Vraag het ons; wij helpen je graag.'
      },
      correctAnswers: 0,
      wrongAnswers: [],
      vraagIndex: undefined,
    }
  },
  methods: {
    reset () {
      this.correctAnswers = 0,
      this.vraagIndex = undefined
      this.wrongAnswers = []
      EventBus.$emit('reset')
    },
    parentHandler (result) {
      if (result === true) {
        this.correctAnswers++
      } else {
        this.wrongAnswers.push("Vraag " + (this.vraagIndex + 1));
      }
      this.vraagIndex++
    },
    start () {
      this.vraagIndex = 0
      EventBus.$emit('disableTagline')
    }
  },
  computed: {
    scoreCorrect () {
      return this.correctAnswers
    },
    totalQuestions () {
      return this.vragen.length
    },
    showPath () {
      if (this.vraagIndex < this.vragen.length ) {
        return true
      } else {
        return false
      }
    },
    scoreResult () {
      if (this.correctAnswers > (this.vragen.length / 2)) {
        return this.scoreResults.above
      } else {
        return this.scoreResults.below
      }
    },
    showScorePage () {
      if (this.vraagIndex === this.vragen.length) {
        EventBus.$emit('disableTagline')
        return true
      }
    }
  },
  mounted () {
    EventBus.$on('reset', this.reset)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 5vh;
    margin-bottom: 20px;
}
h2 {
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 9vh;
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 3px;
}
a {
  color: #42b983;
}


.quiz {
  text-align: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  max-height: 65vh;
  padding: 0 5%;
  justify-content: center;
}

.hide {
  display:none;
}
.dot {
  height: 10px;
  width: 10px;
  background-color: #b3b4b3;
  border-radius: 50%;
  display: inline-block;
}
.active {
  background-color: #1fabe2;
}

.result {
  width: 25%;
  background-color: #b3b4b3;
  border-radius: 10px;
  color: white;
  padding: 5px 20px 25px 20px;
  display: inline-block;
}

.score-result {
  margin-top: 10px;
  font-size: 3vh;
  color: #959595;
}

.cta {
  margin-top: 20px;
}

.locationPath {
  width: 100%;
  align-content: center;
  align-self: flex-end;
}

button {
    font-family: 'Yanone Kaffeesatz', sans-serif;
    font-size: 3.5vh;
    color: white;
    margin: 10px;
    background: #b3b4b3;
    border-radius: 10px;
    padding: 10px 20px;
    border: none;
  }

#opnieuw {
    font-size: 3vh;
    padding: 5px 15px;
    background: #0095ad;
  }

.intro-page {
  align-self: center;
}

</style>



